import '../../node_modules/focus-visible/dist/focus-visible';
import '../scss/main.scss';
import '../index.html';

let brandsReadMore = document.querySelector('.brands__read-more');
let brandsHideMore = document.querySelector('.brands__hide-more');
let brandsList = document.querySelector('.brands__list');

brandsReadMore.addEventListener('click', ()=>{
    brandsReadMore.style.display = 'none';
    brandsHideMore.style.display = 'block';
    brandsList.style.height = 'auto';
});

brandsHideMore.addEventListener('click', ()=>{
    brandsHideMore.style.display = 'none';
    brandsReadMore.style.display = 'block';
    brandsList.style.height = '160px';
});

let servicesList = document.querySelector('.services__list');
let servicesReadMore = document.querySelector('.services__read-more');
let servicesHideMore = document.querySelector('.services__hide-more');

servicesReadMore.addEventListener('click', ()=>{
    servicesReadMore.style.display = 'none';
    servicesHideMore.style.display = 'block';
    servicesList.style.height = 'auto';
});

servicesHideMore.addEventListener('click', ()=>{
    servicesHideMore.style.display = 'none';
    servicesReadMore.style.display = 'block';
    servicesList.style.height = '165px';
})

let textReadMore = document.querySelector('.text__read-more');
let textHideMore = document.querySelector('.text__hide-more');

textReadMore.addEventListener('click', ()=>{
    // document.querySelector('.text__main').style.height = 'auto';
    textReadMore.style.display = 'none';
    textHideMore.style.display = 'block';
});

textHideMore.addEventListener('click', ()=>{
    // document.querySelector('.text__main').style.height = '262px';
    textHideMore.style.display = 'none';
    textReadMore.style.display = 'block';
 });

const modalCloseText = document.querySelector('.modal__close-text');
const modalCloseCall = document.querySelector('.modal__close-call');
const modalWindowCall = document.querySelector('.modal__call');
const modalWindowText = document.querySelector('.modal__text');
// const pricesCall = document.querySelectorAll('.prices__link');
const modalCall2 = document.querySelector('.menu__call');
const menuText = document.querySelector('.menu__text');
const wrapper = document.querySelector('.site-container');
const aside = document.querySelector('.aside');
const menuCall = document.querySelector('.header__tablet-other-call');
const menuChat = document.querySelector('.header__tablet-other-chat');
const menuBurger = document.querySelector(".header__tablet-burger");
const menu = document.querySelector('.header__tablet');
const asideClose = document.querySelector('.burger__close');
const closeAside = document.querySelector('.close-cross');
const body = document.body;

menuChat.addEventListener('click', ()=>{
    modalWindowText.style.right = '0px';
    modalWindowCall.style.right = '-1000px';
    wrapper.classList.add('blur');
    aside.classList.add('blur');
    menu.classList.add('blur');
});

modalCall2.addEventListener('click', ()=>{
    modalWindowCall.style.right = '0px';
    modalWindowText.style.right = '-1000px';
    wrapper.classList.add('blur');
    aside.classList.remove('showAside');
    aside.classList.add('blur');

});

menuCall.addEventListener('click', ()=>{
    aside.classList.remove('showAside');
    modalWindowCall.style.right = '0px';
    modalWindowText.style.right = '-1000px';
    wrapper.classList.add('blur');
    menu.classList.add('blur'); 
});

wrapper.addEventListener('çlick', ()=>{
    aside.classList.remove('blur');
    menu.classList.remove('blur');

})

